import { Typography, Button } from '@material-ui/core'
import React from 'react'
import { HomeCard } from './Styles'
import { Grid } from '@material-ui/core'
import { Link, animateScroll as scroll } from 'react-scroll'
export default function Home() {
    return (
        <Grid container direction='column' justifyContent='center' alignItems='center'>

            <Grid item xs={12}>
                <HomeCard>

                    <Typography variant='h1' style={{ textAlign: 'center', color: 'white', fontFamily: 'Montserrat', fontWeight: '500' }}>Efficient Solutions for a Sustainable Future</Typography>

                    <Link to='learn' smooth={true} duration={600} >
                        <Button variant='contained' size='large' style={{ backgroundColor: '#DE403D', color: 'white', margin: '2rem' }}>Learn More</Button>
                    </Link>
                </HomeCard>
            </Grid>

        </Grid>
    )
}
