import React from 'react'
import { Link, BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { Navbar, Logo, NavLinks, NavLink, Contact } from './Styles'
import { Typography, Button } from '@material-ui/core'
import logo from '../Assets/logo.jpeg'
import { animateScroll as scroll } from 'react-scroll'



export default function Header() {




    return (
        <>
            <Router>
                <Navbar>
                    <NavLink to='home' smooth={true} duration={700}>
                        <NavLink to='header'>
                            <Logo>
                                <Typography variant='h5' style={{ fontFamily: 'Montserrat', color: 'white' }}> Associated Power Systems</Typography>
                            </Logo>
                        </NavLink>
                    </NavLink>
                    <NavLinks>
                        <NavLink to='header' smooth={true} duration={700}>Home</NavLink>
                        <NavLink to='learn' smooth={true} duration={700}>What We Do</NavLink>
                        <NavLink to='products' smooth={true} duration={700}>Products</NavLink>
                        <NavLink t0='about' smooth={true} duration={700}>About Us</NavLink>
                    </NavLinks>

                    <Contact>
                        <Button variant='outlined' size='medium' style={{ borderColor: 'white', color: 'white' }}>Contact Us</Button>
                    </Contact>

                    {/* <ContactButton variant='outlined' size='medium'>
                        <Typography variant='body2' style={{ color: 'white' }}>Contact Us</Typography>
                         </ContactButton> */}
                </Navbar>

            </Router>
        </>
    )
}
