import React, { useState } from 'react'
import { Container, NavLink, ButtonLink } from './Styles'
import { Grid, Typography, Button, hslToRgb } from '@material-ui/core'
import { BrowserRouter as Router, Switch, Route, Redirect, useHistory } from 'react-router-dom'
import { scroller } from 'react-scroll'
import Electrical from '../Components/Electrical'
import Solar from '../Components/Solar'
import ProcessCooling from '../Components/ProcessCooling'
export default function Products({ History }) {


    const history = useHistory();


    return (
        <Container>
            <Router>

                <Grid container alignItems='center' direction='row'>

                    <Grid item xs={12}>
                        <Typography variant='h2' align='center' style={{ fontFamily: 'Montserrat', color: '#4B4C5D', marginTop: '5rem', fontWeight: '500' }}>
                            Products We Offer
                        </Typography>
                    </Grid>

                    <Grid item xs={12} alignItems='center' >
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', textAlign: 'center', margin: '2rem' }}>
                            <ButtonLink to='/'>
                                <Button variant="contaied" style={{ backgroundColor: '#DE403D', color: 'white', margin: '2rem' }}>
                                    Solar
                                </Button>
                            </ButtonLink>



                            <ButtonLink to='/electrical'>
                                <Button variant="outlined" style={{ backgroundColor: '#DE403D', color: 'white', margin: '2rem' }}>Electrical and Lightning</Button>
                            </ButtonLink>


                            <ButtonLink to='/process'>
                                <Button variant="outlined" style={{ backgroundColor: '#DE403D', color: 'white', margin: '2rem' }}>Process Cooling</Button>
                            </ButtonLink>

                        </div>
                    </Grid>

                </Grid>

                <Switch>
                    <Route exact path='/' component={Solar} />
                    <Route exact path='/electrical' component={Electrical} />
                    <Route exact path='/process' component={ProcessCooling} />
                    <Redirect to='/home' />
                </Switch>


            </Router>
        </Container>
    )
}
